import { wixClientServer } from "@/lib/wixClientServer";
import Link from "next/link";

export default async function ProductPage({ categoryId, searchParams }) {
    // Await searchParams here to ensure it's resolved
    searchParams = await searchParams;

    if (!categoryId) {
        return <div>Error: No category ID provided</div>;
    }

    const wixClient = await wixClientServer();

    try {
        const productQuery = wixClient.products
            .queryProducts()
            .startsWith("name", searchParams?.name || "")
            .hasSome("productType", searchParams?.type ? [searchParams.type] : ["physical", "digital"])
            .hasSome("collectionIds", [categoryId])
            .gt("priceData.price", searchParams?.min || 0)
            .lt("priceData.price", searchParams?.max || 9999);

        if (searchParams?.sort) {
            const [sortType, sortBy] = searchParams.sort.split(" ");
            if (sortBy === "price") {
                if (sortType === "asc") {
                    productQuery.ascending("priceData.price");
                } else if (sortType === "desc") {
                    productQuery.descending("priceData.price");
                }
            } else if (sortBy === "lastUpdated") {
                if (sortType === "asc") {
                    productQuery.ascending("lastUpdated");
                } else if (sortType === "desc") {
                    productQuery.descending("lastUpdated");
                }
            }
        }

        const res = await productQuery.find();

        if (!res.items || res.items.length === 0) {
            return <div>No products found for this category.</div>;
        }

        return (
            <div className="product_page">
                <div className="container">
                    <div className="inner_">
                        <ul className="product_list">
                            {res.items.map((product, index) => (
                                <li key={index}>
                                    <Link href={`/${product.slug}`}>
                                        <div className="img_wrap">
                                            <img src={product.media?.items[1]?.image?.url} alt={product.name} />
                                        </div>
                                        <div className="name_price">
                                            <label className="cat_name">{product.name}</label>
                                            <label className="cat_price">${product.price?.price}</label>
                                        </div>
                                        <button className="add_cart">Add to Cart</button>
                                    </Link>
                                </li>
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        );
    } catch (error) {
        console.error("Wix API Error:", error);
        return <div>Error: {error.message || "Failed to fetch products"}</div>;
    }
}
